.v-select {
  border: none;
  border-bottom: dashed;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  display: inline-block;
}
.v-select .dropdown-toggle {
    border: none;
    display: inline-block;
}
.v-select .dropdown-toggle input[type=search], .v-select .dropdown-toggle input[type=search]:focus {
      border: none;
      max-width: 200px;
      float: none;
}
.v-select.single .selected-tag {
    position: absolute;
    line-height: normal;
    font-weight: bold;
    width: 100%;
    float: none;
    margin: inherit;
    color: inherit;
    background-color: inherit;
    border: none;
    border-radius: 0;
    height: auto;
    padding: inherit 10px inherit inherit;
}
